<template>
  <ArticleReader v-if="article" :articleConfig="article" />
  <div v-else class="error-message">
    <h2></h2>
    <p></p>
    <router-link to="/ListTry50"></router-link>
  </div>
</template>

<script>
import ArticleReader from '@/components/ArticleReader.vue';
import { getArticleByRoute } from '@/data/articles';

export default {
  name: 'ArticlePage',
  components: {
    ArticleReader
  },
  data() {
    return {
      article: null
    };
  },
  created() {
    // 
    const currentRoute = this.$route.path;
    this.article = getArticleByRoute(currentRoute);
    
    if (!this.article) {
      console.error(':', currentRoute);
    }
  },
  watch: {
    // ,
    '$route.path'(newPath) {
      this.article = getArticleByRoute(newPath);
      if (!this.article) {
        console.error(':', newPath);
      }
    }
  }
};
</script>

<style scoped>
.error-message {
  text-align: center;
  padding: 2rem;
  color: #e74c3c;
}

.error-message h2 {
  margin-bottom: 1rem;
}

.error-message a {
  color: #007bff;
  text-decoration: none;
}

.error-message a:hover {
  text-decoration: underline;
}
</style>